body{
  background: #ddd;
  /* z-index: -2; */
}
#root{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.todoInput{
   margin-bottom: 0.5em; 
}
.add-btn{
    margin-left: 0.5em;
    border-radius: 0.1em;
    background: #ccc;
    cursor: pointer;
}
#todoItem{
  margin-top: .3em;
  padding: 0.2em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

#todoItem>#todo{
    -webkit-flex-grow: 1;
            flex-grow: 1;
}
#todoItem input[type=checkbox]{
   margin-right: 0.2em; 
}
#todoItem button{
   cursor: pointer;
}

.App {
  color: #333;
  border: 1px solid #aaa;
  background: white;
  margin: 2em;
  padding: 0 1em;
  min-height:30em;
  min-width: 25em;
  box-shadow: 0 0 1em hsla(0,0%,0%,.15);
}
.App>.title{
  display:table;
}
.App>.title>button{
  font: 0.6em bold Arial;
  margin-left: 0.8em;
  padding: 0.1em 0.2em;
  color:red;
  vertical-align: middle;
  border-radius: 0.2em;
  background: #ccc;
  cursor: pointer;
}
.App > .todoList > li:nth-child(odd){

  background: #eee;
}
.App > .todoList > li:nth-child(even){
  background: #aaa;
}
