*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
ul,dl,ol{
    list-style: none;
}
a{
    color: inherit;
}

/* .UserDialog-Wrapper{ 
   position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
   display: flex; justify-content: center; align-items: center;
  }
  */
.UserDialog-Wrapper{
  /*全屏*/      position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
  /*内容居中*/  display: -webkit-flex;  display: flex; -webkit-justify-content: center; justify-content: center; -webkit-align-items: center; align-items: center;
  background: hsla(0,0%,0%,0.40);
}
.UserDialog{
min-width: 20em;
background: white;
border: 1px solid #aaa;
border-radius: 4px;
box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
}
.UserDialog nav:first-child{ 
    padding: 1em; 
    margin-left: -2em;
}
.UserDialog  nav:first-child label{
    margin-left: 2em;
    cursor: pointer;
}
/* .UserDialog > nav:first-child input[type=radio]:not(:first-child){  */
.UserDialog  nav >input:nth-child(2){
    margin-left: 2em;
}

.UserDialog  .panes { 
    padding: 0 1em 1em; 
}

.UserDialog form>.row{ display: -webkit-flex; display: flex; padding: .3em 0; }
.UserDialog form>.row>label:first-child{ width: 4em; }
.UserDialog form>.row.actions{ padding-left: 4em; }
.UserDialog form>.row.actions>.forgetPass{
    margin-left: 1em;
}
.UserDialog > .forgotPassword{
   padding: 0.5em 1em;
}
.UserDialog form>.row.actions>a{
    margin-left: 0.5em;
}
    




